
import { defineComponent, reactive, toRefs, SetupContext } from "vue";
interface Data {
  msg: string;
}
export default defineComponent({
  name: "",
  props: {},
  components: {},
  setup(props, ctx: SetupContext) {
    const data: Data = reactive<Data>({ msg: "" });
    const confirm = (text:string) => {
      console.log(text, '')
    }
    return {
      ...toRefs(data),confirm
    };
  },
});
